@import url('https://fonts.googleapis.com/css2?family=Jost:wght@400;700&display=swap');

.box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.greybox {
  width: 188px;
  height: 46px;
  background-color: rgba(217, 217, 217, 0.5);
  display: flex;
  flex-direction: column;
  align-items: center;
  border-left: 30%;
  border-right: 30%;
}

.name {
  font-family: 'Jost';
  font-weight: 800;
  font-size: 1vw;
  //text-shadow: 0.1px 0.1px 0.1px black;
}

.tag {
  opacity: 0.7;
  font-style: italic;
  font-size: .7vw;
}

.bot {
  margin-top: 4%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav {
  font-family: 'Jost';
  padding-left: 20%;
  padding-right: 20%;
}